import React, { useState, useEffect } from 'react';
import CustomInput from './CustomInput';
import CustomButton from './CustomButton';
import axios from 'axios';
import './LabEditPopUp.css';

const LabEditPopUp = ({ closePopup, labId }) => {
    const [laboratorios, setLaboratorios] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [showSuccess, setShowSuccess] = useState(false);
    const [error, setError] = useState(null);

    const getCookie = (name) => {
        const value = `; ${document.cookie}`;
        const parts = value.split(`; ${name}=`);
        if (parts.length === 2) return parts.pop().split(';').shift();
    };

    useEffect(() => {
        const fetchLabData = async () => {
            try {
                const response = await axios.get(`http://127.0.0.1:8000/api/lab-by-id/?id_sala=${labId}`, {
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem('access_token')}`,
                    },
                });
                setLaboratorios(response.data || []);
                 console.log("Laboratrios recebidos:", response.data);
                 console.log(laboratorios);
            } catch (error) {
                setError('Erro ao carregar dados do laboratrio.');
            }
        };

        fetchLabData();
    }, [labId]);


    const handleEditLab = async () => {
        setIsLoading(true);
        try {
            const csrfToken = getCookie('csrftoken');
            await axios.put(`http://127.0.0.1:8000/api/lab-update/${labId}/`, {
                nome: laboratorios.nome,
                sala_ou_bloco: laboratorios.sala_ou_bloco,
            }, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('access_token')}`,
                    'X-CSRFToken': csrfToken,
                },
            });

            setShowSuccess(true);
            setTimeout(() => setShowSuccess(false), 20000);
        } catch (error) {
            setError("Erro ao editar laboratrio: " + (error.response?.data?.detail || error.message));
        } finally {
            setIsLoading(false);
        }
    };

    const isButtonDisabled = !(laboratorios.nome && laboratorios.sala_ou_bloco);

    return (
        <>
            <div className="popup-container">
                <h2 className="popup-title-lab">Editar Laboratrio</h2>
                <button className="close-btn" onClick={closePopup}>
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <g clipPath="url(#clip0_2033_6804)">
                            <path d="M7 7L17 17M7 17L17 7" stroke="black" strokeOpacity="0.61" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                        </g>
                        <defs>
                            <clipPath id="clip0_2033_6804">
                                <rect width="24" height="24" fill="white" />
                            </clipPath>
                        </defs>
                    </svg>
                </button>

                <div className='popup-lab-input'>
                    <div>
                        <CustomInput
                            label="Nome"
                            placeholder="Digite o Nome do Laboratrio"
                            className="input-field300"
                            value={laboratorios.nome}
                            onChange={(e) => setLaboratorios({ ...laboratorios, nome: e.target.value })}
                        />
                    </div>
                    <div className='input-sala'>
                        <CustomInput
                            label="Sala/Bloco"
                            placeholder="Digite a Sala e Bloco"
                            className="input-field300"
                            value={laboratorios.sala_ou_bloco}
                            onChange={(e) => setLaboratorios({ ...laboratorios, sala_ou_bloco: e.target.value })}
                        />
                    </div>
                </div>

                <div className="popup-actions-confirm">
                    {isLoading ? (
                        <svg /* spinner svg aqui */ />
                    ) : (
                        <CustomButton
                            disabled={isButtonDisabled}
                            label="Editar"
                            className="blue size138"
                            onClick={handleEditLab}
                        />
                    )}
                    {showSuccess && (
                        <div className="success-message">
                            Laboratrio atualizado com sucesso!
                        </div>
                    )}
                    {error && (
                        <div className="error-message">
                            {error}
                        </div>
                    )}
                </div>
            </div>
        </>
    );
};

export default LabEditPopUp;
